                    <b-form-input :state="validatePAY_1"  class="form-control rounded-pill pad col-md-3 col-sm-6" type="number" v-model.trim="defaultdata.PAY_1" placeholder="2 months ago"></b-form-input>
                    <b-form-input :state="validatePAY_2"  class="form-control rounded-pill pad col-md-3 col-sm-6" type="number" v-model.trim="defaultdata.PAY_2" placeholder="3 months ago"></b-form-input>
                    <b-form-input :state="validatePAY_3"  class="form-control rounded-pill pad col-md-3 col-sm-6" type="number" v-model.trim="defaultdata.PAY_3" placeholder="4 months ago"></b-form-input>
                    <b-form-input :state="validatePAY_4"  class="form-control rounded-pill pad col-md-3 col-sm-6" type="number" v-model.trim="defaultdata.PAY_4" placeholder="5 months ago"></b-form-input> 
                    <b-form-input :state="validatePAY_5"  class="form-control rounded-pill pad col-md-3 col-sm-6" type="number" v-model.trim="defaultdata.PAY_5" placeholder="6 months ago"></b-form-input>
                    <b-form-invalid-feedback :state="validatePAY_0"></b-form-invalid-feedback>
                    <b-form-valid-feedback :state="validatePAY_0"></b-form-valid-feedback>                  
                    <b-form-invalid-feedback :state="validatePAY_1"></b-form-invalid-feedback>
                    <b-form-valid-feedback :state="validatePAY_1"></b-form-valid-feedback>
                    <b-form-invalid-feedback :state="validatePAY_2"></b-form-invalid-feedback>
                    <b-form-valid-feedback :state="validatePAY_2"></b-form-valid-feedback>
                    <b-form-invalid-feedback :state="validatePAY_3"></b-form-invalid-feedback>
