                        </h4>
                        <p class="card-text">
                            Each cell is the bill for a specific month in dollars.
                            <br>The 1st cell is your bill for 1 month ago, the 2nd cell for 2 months ago etc.
                        </p>
                        </b-card>
                    </b-collapse>
                </div>
                <div class="form-row" style="max-width:50rem">
                    <b-form-input :state="validateBILL_AMT1" class="form-control rounded-pill pad col-md-3 col-sm-6" type="number" v-model.trim="defaultdata.BILL_AMT1" placeholder="1 month ago"></b-form-input>
                    <b-form-input :state="validateBILL_AMT2" class="form-control rounded-pill pad col-md-3 col-sm-6" type="number" v-model.trim="defaultdata.BILL_AMT2" placeholder="2 months ago"></b-form-input>
                    <b-form-input :state="validateBILL_AMT3" class="form-control rounded-pill pad col-md-3 col-sm-6" type="number" v-model.trim="defaultdata.BILL_AMT3" placeholder="3 months ago"></b-form-input>
                    <b-form-input :state="validateBILL_AMT4" class="form-control rounded-pill pad col-md-3 col-sm-6" type="number" v-model.trim="defaultdata.BILL_AMT4" placeholder="4 months ago"></b-form-input>
                    <b-form-input :state="validateBILL_AMT5" class="form-control rounded-pill pad col-md-3 col-sm-6" type="number" v-model.trim="defaultdata.BILL_AMT5" placeholder="5 months ago"></b-form-input>
                    <b-form-input :state="validateBILL_AMT6" class="form-control rounded-pill pad col-md-3 col-sm-6" type="number" v-model.trim="defaultdata.BILL_AMT6" placeholder="6 months ago"></b-form-input>
                    <b-form-invalid-feedback :state="validateBILL_AMT1"></b-form-invalid-feedback>
                    <b-form-valid-feedback :state="validateBILL_AMT1"></b-form-valid-feedback>
                    <b-form-invalid-feedback :state="validateBILL_AMT2"></b-form-invalid-feedback>
                    <b-form-valid-feedback :state="validateBILL_AMT2"></b-form-valid-feedback>    
                    <b-form-invalid-feedback :state="validateBILL_AMT3"></b-form-invalid-feedback>
                    <b-form-valid-feedback :state="validateBILL_AMT3"></b-form-valid-feedback>    
                    <b-form-invalid-feedback :state="validateBILL_AMT4"></b-form-invalid-feedback>
                    <b-form-valid-feedback :state="validateBILL_AMT4"></b-form-valid-feedback>    
                    <b-form-invalid-feedback :state="validateBILL_AMT5"></b-form-invalid-feedback>
                    <b-form-valid-feedback :state="validateBILL_AMT5"></b-form-valid-feedback>    
                    <b-form-invalid-feedback :state="validateBILL_AMT6"></b-form-invalid-feedback>
                    <b-form-valid-feedback :state="validateBILL_AMT6"></b-form-valid-feedback>    
                </div><br><br>

