                        <b-row>
                            <b-col>
                                <h3>Monthly payment</h3>
                            </b-col>
                            <b-col>
                                <b-button pill variant="dark" v-b-toggle.collapse-4>What's this?</b-button>
                            </b-col>
                        </b-row>
                    </b-container>
                    <b-collapse id="collapse-4" class="mt-2">
                        <b-card>
                        <h4 class="card-text">
                            <b>It's your how much of your credit card bill you paid back each month.</b>
                        </h4>
                        <p class="card-text">
                            Each cell is the bill for a specific month in dollars.
                            <br>The 1st cell is your payment for 1 month ago, the 2nd cell for 2 months ago etc.
                        </p>
                        </b-card>
                    </b-collapse>
                </div>
                <div class="form-row" style="max-width:50rem">
