                    <b-form-input :state="validatePAY_AMT2" class="form-control rounded-pill pad col-md-3 col-sm-6" type="number" v-model.trim="defaultdata.PAY_AMT2" placeholder="2 months ago"></b-form-input>
                    <b-form-input :state="validatePAY_AMT3" class="form-control rounded-pill pad col-md-3 col-sm-6" type="number" v-model.trim="defaultdata.PAY_AMT3" placeholder="3 months ago"></b-form-input>
                    <b-form-input :state="validatePAY_AMT4" class="form-control rounded-pill pad col-md-3 col-sm-6" type="number" v-model.trim="defaultdata.PAY_AMT4" placeholder="4 months ago"></b-form-input>
                    <b-form-input :state="validatePAY_AMT5" class="form-control rounded-pill pad col-md-3 col-sm-6" type="number" v-model.trim="defaultdata.PAY_AMT5" placeholder="5 months ago"></b-form-input>
                    <b-form-input :state="validatePAY_AMT6" class="form-control rounded-pill pad col-md-3 col-sm-6" type="number" v-model.trim="defaultdata.PAY_AMT6" placeholder="6 months ago"></b-form-input>
