                    <b-form-valid-feedback :state="validatePAY_AMT5"></b-form-valid-feedback>    
                    <b-form-invalid-feedback :state="validatePAY_AMT6"></b-form-invalid-feedback>
                    <b-form-valid-feedback :state="validatePAY_AMT6"></b-form-valid-feedback>  
