<script>
import { getAPI } from "@/axios";
export default {
    data() {
        return{
            defaultdata: {
                LIMIT_BAL: "",
                SEX: "",
                EDUCATION: "",
                MARRIAGE: "",
                AGE: "",
                PAY_0: "",
                PAY_1: "",
                PAY_2: "",
                PAY_3: "",
                PAY_4: "",
                PAY_5: "",
                BILL_AMT1: "",
                BILL_AMT2: "",
                BILL_AMT3: "",
                BILL_AMT4: "",
                BILL_AMT5: "",
                BILL_AMT6: "",
                PAY_AMT1: "",
                PAY_AMT2: "",
                PAY_AMT3: "",
                PAY_AMT4: "",
                PAY_AMT5: "",
                PAY_AMT6: ""
            },
            APIResult:[],
        };
    },
    computed: {
        validateLIMIT_BAL(){if(!this.defaultdata.LIMIT_BAL || this.defaultdata.LIMIT_BAL<0){return false;} return true;},
        validateSEX(){if(!this.defaultdata.SEX){return false;} return true;},
        validateEDUCATION(){if(!this.defaultdata.EDUCATION){return false;} return true;},
        validateMARRIAGE(){if(!this.defaultdata.MARRIAGE){return false;} return true;},
        validateAge(){if(!this.defaultdata.AGE || this.defaultdata.AGE<0){return false;} return true;},
        validatePAY_0(){if(!this.defaultdata.PAY_0){return false;} return true;},
        validatePAY_1(){if(!this.defaultdata.PAY_1){return false;} return true;},
        validatePAY_2(){if(!this.defaultdata.PAY_2){return false;} return true;},
        validatePAY_3(){if(!this.defaultdata.PAY_3){return false;} return true;},
        validatePAY_4(){if(!this.defaultdata.PAY_4){return false;} return true;},
        validatePAY_5(){if(!this.defaultdata.PAY_5){return false;} return true;},
        validateBILL_AMT1(){if(!this.defaultdata.BILL_AMT1){return false;} return true;},
        validateBILL_AMT2(){if(!this.defaultdata.BILL_AMT2){return false;} return true;},
        validateBILL_AMT3(){if(!this.defaultdata.BILL_AMT3){return false;} return true;},
        validateBILL_AMT4(){if(!this.defaultdata.BILL_AMT4){return false;} return true;},
        validateBILL_AMT5(){if(!this.defaultdata.BILL_AMT5){return false;} return true;},
        validateBILL_AMT6(){if(!this.defaultdata.BILL_AMT6){return false;} return true;},
        validatePAY_AMT1(){if(!this.defaultdata.PAY_AMT1){return false;} return true;},
        validatePAY_AMT2(){if(!this.defaultdata.PAY_AMT2){return false;} return true;},
        validatePAY_AMT3(){if(!this.defaultdata.PAY_AMT3){return false;} return true;},
        validatePAY_AMT4(){if(!this.defaultdata.PAY_AMT4){return false;} return true;},
        validatePAY_AMT5(){if(!this.defaultdata.PAY_AMT5){return false;} return true;},
        validatePAY_AMT6(){if(!this.defaultdata.PAY_AMT6){return false;} return true;},
    },
    methods: {
        predict() {
            getAPI.get("/", {
                params: {
                    limitbal: this.defaultdata.LIMIT_BAL,
                    gender: this.defaultdata.SEX,
                    education: this.defaultdata.EDUCATION,
                    marriage: this.defaultdata.MARRIAGE,
                    age: this.defaultdata.AGE,
                    pay0: this.defaultdata.PAY_0,
                    pay1: this.defaultdata.PAY_1,
                    pay2: this.defaultdata.PAY_2,
                    pay3: this.defaultdata.PAY_3,
                    pay4: this.defaultdata.PAY_4,
                    pay5: this.defaultdata.PAY_5,
                    billamt1: this.defaultdata.BILL_AMT1,
                    billamt2: this.defaultdata.BILL_AMT2,
                    billamt3: this.defaultdata.BILL_AMT3,
                    billamt4: this.defaultdata.BILL_AMT4,
                    billamt5: this.defaultdata.BILL_AMT5,
                    billamt6: this.defaultdata.BILL_AMT6,
                    payamt1: this.defaultdata.PAY_AMT1,
                    payamt2: this.defaultdata.PAY_AMT2,
                    payamt3: this.defaultdata.PAY_AMT3,
                    payamt4: this.defaultdata.PAY_AMT4,
                    payamt5: this.defaultdata.PAY_AMT5,
                    payamt6: this.defaultdata.PAY_AMT6
                }
            }).then(response => {
                this.APIResult = [response.data];
                //console.log("Below is the response.data : ")
                //console.log(response.data);
            }).catch(err => {
                console.log(err);
            });
        }
    }
};
</script>

<style>
.jumbotron-image { background-position: center center; background-repeat: no-repeat; background-size: cover }
.pad { margin:5px; padding:5px; }
.pad2 { margin-bottom: 5px; margin-top: 5px; padding: 5px;}
.form-row{ justify-content: center ;}
#highrisk{color: rgb(126, 0, 0);}
#medrisk{color: rgb(0, 0, 214)}
#lowrisk{color: rgb(0, 92, 0)}
</style>